/**********************************
VARIABLES
***********************************/
$color-blue-dark: #769FCD;
$color-blue: #B9D7EA;
$color-blue-light: #D6E6F2;
$color-white: #F7FBFC; 

$color-1: saturate($color-blue-dark, 20%);


// $color-blue: #3982D3;
// $color-blue-light: #87B6EA;
$color-green: #2CD4A1;
$color-green-light: #7FEACA;

$color-grey: #829399;
$color-grey-dark: #545F66;


%clearfix {
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}


/**********************************
GENERAL
***********************************/

* {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  // color: #B9D7EA;
  color: rgba(0, 0, 0, 0.7);
  background: #F7FBFC;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: $color-blue-dark;
  outline: none;
}

img {
  max-width: 100%;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style-position: inside;
  list-style-type: circle;
}

li {
  padding: .3em 0;
}



/**********************************
HOME PAGE
***********************************/

.social-icons {
  // display: none;
  position: fixed;
  right: -100px;
  // left: 30px;
  bottom: 30px;
  z-index: 9999;
  transition: right 0.4s ease-out;
  
  
  li {
    list-style-type: none;
    span {
      display: inline-block;
      border-radius: 100%;
      text-align: center;
      vertical-align: middle;
      width: 100%;
      height: 100%;
      background: $color-blue;
      transition: all 0.2s ease-out;

      &:hover {
        background: $color-blue-light;
        transform: scale(1.1, 1.1);
        &:before {
          display: inline-block;
          position: absolute;
          right: 50px;
          line-height: 20px;
          top: 10px;
          padding: 3px 8px;
          border-radius: 2px;
          background: $color-blue-dark;
          color: $color-white;
          transform: scale(1, 1);
        }
        &[title='GitHub'] {
          &:before {
            content: 'GitHub';
          } 
        }
        &[title='LinkedIn'] {
          &:before {
            content: 'LinkedIn';
          } 
        }
        &[title='Email'] {
          &:before {
            content: 'claytonlocher@gmail.com';
          } 
        }

        i {
          color: $color-blue-dark;
        }
      }
      i {
        color: #fff;
        width: 45px;
        height: 45px;
        line-height: 45px;
      }
    }
  }
}

.content-container {
  width: 100%;
  padding: 0 10%;
  margin-bottom: 2em;
  clear: both;
}

.section-title {
  color: $color-blue;
  font-weight: bold;

  &:after {
    content: '';
    display: inline-block;
    position: relative;
    vertical-align: middle;
    height: 10px;
    width: 10px;
    border-radius: 10px;
    background: $color-blue-light;
    background: $color-blue-light;
  }

  &:before {
    right: .4em;
  }

  &:after {
    left: .2em;
    vertical-align: baseline;
  }
}

.top-container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: #F7FBFC;
  position: relative;

  #main-logo {
    max-width: 100%;
    height: 12em;
    display: inline-block;
  }

  .tagline {
    margin-top: 4em;
    & > span {
      display: inline-block;
      font-family: 'Mukta Vaani', sans-serif;
      font-weight: 300;
      font-size: 2.4rem;
      text-align: center;
    }
  }

  .down-arrow {
    position: absolute;
    display: inline-block;
    bottom: 10px;
    text-align: center;
    width: 100%;
    color: $color-blue;
  }

}

.card {
  @extend %clearfix;
  background: #fff;
  // color: #aaa;
  color: #555;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .1);
  margin-bottom: 5%;
  padding: 5%;
  clear: both;
  border-radius: 3px;
}

.project-header {
  width: 100%;
  flex: 1 0 auto;

  h3 {
    color: #769FCD;
    color: $color-blue-dark;
    font-size: 2em;
    margin-bottom: .5em;
  }

}

.project-timeline {
  color: $color-blue-dark;
  border-top: 2px solid #eee;
  border-bottom: 2px solid #eee;
  padding: .3em 0 .3em 0;
  vertical-align: 30%;
}

.col {
  float: left;
  width: 55%;

  &:first-of-type {
    padding-right: 2.5%;
    width: 45%;

  }

  &:nth-of-type(2n) {
    padding-left: 2.5%;
  }

  a {
    & img {
      margin-top: 5%;
    }
    &:first-of-type img {
      margin-top: 1em;
    }
    &:nth-of-type(2n + 2) img {
      margin-right: 5%;
    }
  }


}


.website-link {
  display: inline-block;
  margin: .5em 0;
  padding: .5em .8em;
  background: $color-blue-dark;
  color: #fff !important;
  font-size: 1.1em;
  border: 1px solid $color-blue;
  border-radius: 2px;
  vertical-align: 30%;
  transition: all 0.2s ease-out;
  &:hover {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .15);
    span {
     padding-left: .4em;
    }
  }
  span {
    display: inline-block;
    padding-left: .2em;
    transition: padding 0.2s ease-in-out;
  }


  i {
    padding-left: .2em;
    vertical-align: middle;

  }
}



h2 {
  color: #769FCD;
  color: $color-blue-light;
  text-align: center;
  /*font-family: 'Mukta Vaani', sans-serif;*/
  font-size: 2.5em;
  font-weight: normal;
  /*text-transform: lowercase;*/
  margin: 3em auto 1.2em auto;
}

p {
  font-size: 1.1em;
  line-height: 1.9;
  /*width: 40%;*/
}

.tech-stack {
  display: inline-block;
  margin-top: 1em;

  ul {
    padding: .5em 0;
  }

  ul li {
    display: inline-block;
    color: #fff;
    // background: $color-green-light;
    border: 2px solid $color-green-light;
    color: $color-green;

    padding: .3em .6em;
    margin: .2em .2em .4em 0;

    border-radius: 2px;
    font-size: 1.1em;
  }

}

.screenshot {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, .15);
  text-align: center;
  float: left;
  transition: all 0.2s ease-out;

  &:hover {
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, .30);
    transform: scale(1.02, 1.02);
  }
  
  &.mobile {
    width: 47.5%;
    
  }

}

.about {
  width: 70%;
  margin: 0 auto;
  p:nth-of-type(n + 2) {
    clear: both;
  }
}


#headshot {
  width: 200px;
  border-radius: 100%;
  float: left;
  border: 5px solid #eee;
  margin: 0 2em 2em 0;
}

footer {
  width: 70%;
  margin: 0 auto;
  padding-bottom: .5em;
  color: $color-blue;
  #copyright {
    float: right;
  }
}

